.news-item {
    @include clr;
    margin: 0px 0 30px 0;
    @include sm {
        margin: 22px 0 25px 0; }
    &__pic {
        float: left;
        margin-right: 20px;
        max-width: 165px;

        text-align: center;
        position: relative;
        width: 100%;
        display: table;
        @include sm {
            float: initial;
            margin-bottom: 1em;
            width: 100%;
            max-width: 100%; }

        img {
            width: 100%;
            height: auto; } }
    &__title {
        font-weight: normal;
        color: #333333;
        font-size: 18px;
        text-transform: uppercase;
        margin-bottom: 10px; }
    &__date {
        color: #ABABAB;
        margin-bottom: 4px;
        font-size: 16px;
        margin-bottom: 15px; }
    &__text {
        font-size: 16px;
        color: #949494; } }

.innerPage .page__row.news-oneItem {
    .col-photo {
        width: 330px;
        @include sm {
            width: 100%; } }
    .col-content {
        width: 80%;
        @include sm {
            width: 100%; } } }


