main {
    margin-bottom: 1em;
    padding-bottom: 1em; }
.page__hero {
    @include clr;
    margin-bottom: 1em; }


.page__title {
    @include clr;
    margin-bottom: .5em;
    //margin-left: -13px;
    h1 {
        margin-bottom: 0; }
    &-text {
        float: left;
        line-height: 38px;
        @include md {
            float: left; }
        @include sm {
            float: none; } }
    &-right {
        float: right;
        @include md {
            float: left;
            margin-left: 1em; }
        @include sm {
            margin-left: 0;
            float: none; } } }

.page__title {
    .title__viewAll {
        display: inline-block;
        opacity: .5;
        font-size: 40%;
        margin-left: 10px;
        @include sm {
            display: block;
            margin: 0; } }
    &-right {}

    &-menu {
        float: right;
        font-size: 16px;
        margin-top: 1em;
        margin-bottom: 0;
        @include sm {
            float: none; }
        li {
            float: left;
            margin-right: 1em;
            margin-bottom: 0;
            list-style: none;
            @include sm {
                margin-left: 0; }
            a {
                color: #ABABAB; } }
        .active {
            a {
                color: #333333;
                &:after {
                    border-color: #333333; } } } } }


@import "pages/page__catalog";
@import "pages/page__look";
@import "pages/page__oneitem";

@import "pages/page__actions-news";
@import "pages/page__blog";
@import "pages/page__styleguide";
@import "pages/page__innerpage";
@import "pages/page__404-500";
@import "pages/page__stores";
@import "pages/page__mirmdrt";
@import "pages/page__cabinet";




.page__fullpage .section {
    background: yellow;
    &:nth-of-type(even) {
      background: crimson; }

    &.with-overflow {
        .overflow-content {
            height: 200vh; } } }









.page__news {
    @include clr;
    margin-top: .25em;
    margin-bottom: 3em;
    .news {
        &__item {
            float: left;
            width: 48%;
            h5 {
                font-size: 18px;
                font-weight: normal;
                color: #333333;
                margin-bottom: .5em; }
            p {
                color: #ABABAB;
                font-size: 16px!important;
                margin-bottom: 5px; }

            a, .more {
                color: #333333;
                font-size: 16px!important; } } } }
